// Import necessary modules
import { Core } from '@walletconnect/core';
import { WalletKit } from '@reown/walletkit';
import QRCode from 'qrcode';

let walletKit;

// Function to connect via Wallet Extension (e.g., MetaMask)
async function connectWithExtension() {
  try {
    console.log('Attempting to connect using the wallet extension...');

    if (window.ethereum) {
      console.log('MetaMask detected! Requesting account access...');
      const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
      console.log(`Connected with MetaMask! Account: ${accounts[0]}`);
      document.getElementById('walletInfo').innerHTML = `Connected: <strong>${accounts[0]}</strong>`;
    } else {
      console.error('MetaMask not detected!');
      alert('MetaMask extension is not installed.');
    }
  } catch (error) {
    console.error('Error connecting with extension:', error);
  }
}

// Function to connect via QR Code (WalletConnect)
async function connectWithQRCode() {
  try {
    console.log('Attempting to connect wallet via QR code...');

    // Fetch the pairing URI from the backend
    console.log('Fetching pairing URI from the backend...');
    const response = await fetch('http://localhost:3000/pairing-uri');
    if (!response.ok) {
      console.error(`Failed to fetch pairing URI. Status: ${response.status}`);
      return;
    }

    const data = await response.json();
    let pairingUri = data.uri;

    // Validate and trim the URI before processing
    if (typeof pairingUri !== 'string' || !pairingUri.trim()) {
      console.error('Invalid pairing URI:', pairingUri);
      return;
    }
    pairingUri = pairingUri.trim();
    console.log(`Received pairing URI: ${pairingUri}`);

    // Generate the QR code
    const qrcodeCanvas = document.createElement('canvas');
    const qrcodeDiv = document.getElementById('qrcode');
    qrcodeDiv.innerHTML = ''; // Clear any existing QR code
    qrcodeDiv.appendChild(qrcodeCanvas); // Append the canvas to the div

    QRCode.toCanvas(qrcodeCanvas, pairingUri, (error) => {
      if (error) {
        console.error('Error generating QR code:', error);
      } else {
        console.log('QR code generated successfully.');
      }
    });

    // **WalletConnect Session Events**
    walletKit.on('session_proposal', (proposal) => {
      console.log('Session proposal received:', proposal);
    });

    walletKit.on('session_approved', (session) => {
      console.log('Session approved:', session);
    });

    walletKit.on('session_rejected', (session) => {
      console.error('Session rejected:', session);
    });

    walletKit.on('session_connected', (session) => {
      console.log('Session connected:', session);
    });

    walletKit.on('session_disconnected', (session) => {
      console.error('Session disconnected:', session);
    });

    walletKit.on('session_error', (error) => {
      console.error('Session error:', error);
    });

    console.log('Waiting for user to scan QR code and approve connection...');
  } catch (error) {
    console.error('Error connecting with QR code:', error);
  }
}

// Initialize WalletKit
async function initWalletKit() {
  try {
    console.log('Initializing WalletKit...');
    const core = new Core({
      projectId: '3381e93112924a5ebd64bcb174d37c56', // Replace with your projectId
    });

    walletKit = await WalletKit.init({
      core,
      metadata: {
        name: 'Demo App',
        description: 'Demo Client as Wallet',
        url: 'https://yourapp.com', // Replace this with your actual app URL or extension URL
        icons: [],
      },
    });

    console.log('WalletKit initialized successfully.');
  } catch (error) {
    console.error('Error initializing WalletKit:', error);
  }
}

// Set up event listeners when the page loads
window.onload = function () {
  console.log('Page loaded. Setting up event listeners.');

  const connectExtensionButton = document.getElementById('connectExtension');
  if (connectExtensionButton) {
    connectExtensionButton.addEventListener('click', connectWithExtension);
    console.log('Connect with Extension button event listener added.');
  } else {
    console.error('Connect with Extension button not found.');
  }

  const connectQRCodeButton = document.getElementById('connectQRCode');
  if (connectQRCodeButton) {
    connectQRCodeButton.addEventListener('click', connectWithQRCode);
    console.log('Connect with QR Code button event listener added.');
  } else {
    console.error('Connect with QR Code button not found.');
  }

  // Initialize WalletKit when the page loads
  initWalletKit();
};









































